.row {
  @include clear-fix;

  &--t-padding {
    padding-top: 80px;
  }
  &--gutters {
    margin-right: -4rem;
  }

  &--gutters-large {
    margin-right: -6.25rem;
  }

  &--gutters-small {
    margin-right: -45px
  }

  &--gutters>div {
    padding-right: 4rem;
  }

  &--gutters-large>div {
    padding-right: 6.25rem;
  }

  &--gutters-small > div {
    padding-right: 45px
  }

  &__b-margin-until-medium {
    margin-bottom: 1rem;
  }

  @include atMedium {

    &__b-margin-until-medium {
      margin-bottom: 0;
    }

    &__medium-4 {
      float: left;
      width: 33.33%;
    }

    &__medium-4-larger {
      width: 37%;
    }

    &__medium-8 {
      float: left;
      width: 66.66%;
    }

    &__medium-8-smaller {
      width: 63%;
    }

    &__medium-6 {
      float: left;
      width: 50%;
    }
  }

  @include atLarge {

    &__b-margin-until-large {
      margin-bottom: 0;
    }

    &__large-4 {
      float: left;
      width: 33.33%;
    }

    &__large-4-larger {
      width: 37%;
    }

    &__large-8 {
      float: left;
      width: 66.66%;
    }

    &__large-8-smaller {
      width: 63%;
    }

    &__large-6 {
      float: left;
      width: 50%;
    }
  }
  

    @include atLarge {
      &--equal-height-at-large-screen {
        display: flex;
      & >div {
        float: none;
        display: flex;
      }
    }
  }
}