@mixin atSmall {
  @media(min-width: 530px) {
    @content;
  }
}

@mixin atMedium {
  @media(min-width: 800px) {
    @content;
  }
}

@mixin atLarge {
  @media(min-width: 1010px) {
    @content;
  }
}


@mixin clear-fix {
  &::after {
    content: "";
    clear: both;
    display: table;
  }
}