body {
  font-family: "Roboto", 'sans-serif';
  color: #333; }

img {
  max-width: 100%;
  height: auto; }

.container {
  padding: 0 15px;
  max-width: 1230px;
  margin: auto;
  overflow: hidden; }
  .container--no-padding-until-large {
    padding: 0; }
    @media (min-width: 1010px) {
      .container--no-padding-until-large {
        padding: 0 15px; } }

.text-center {
  text-align: center; }

.b-margin {
  margin-bottom: 1rem; }
  @media (min-width: 530px) {
    .b-margin {
      margin-bottom: 3.875rem; } }

a {
  color: #d59541; }

.large-hero {
  position: relative;
  border-bottom: 10px solid #2f5572; }
  .large-hero__image {
    display: block; }
  .large-hero__text-content {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    transform: translate(-50%, -50%); }
  .large-hero__title {
    font-size: 2.4rem;
    font-weight: 300;
    color: #2f5572;
    margin: 0; }
    @media (min-width: 530px) {
      .large-hero__title {
        font-size: 4.8rem; } }
  .large-hero__subtitle {
    font-size: 1.5rem;
    font-weight: 300;
    color: #2f5572;
    margin: 0; }
    @media (min-width: 530px) {
      .large-hero__subtitle {
        font-size: 2.5rem; } }
  .large-hero__description {
    color: #fff;
    font-size: 1.1rem;
    font-weight: 100;
    max-width: 30rem;
    text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.1);
    margin: 1rem auto; }
    @media (min-width: 530px) {
      .large-hero__description {
        font-size: 2rem; } }

.btn {
  padding: 0.75rem 1.2rem;
  background-color: #2f5572;
  color: #fff;
  text-decoration: none;
  display: inline-block; }
  .btn__orange {
    background-color: #d59541; }
  .btn__large {
    padding: 1.1rem 1.9rem; }
    @media (min-width: 530px) {
      .btn__large {
        font-size: 1.2rem; } }

.headline {
  font-weight: 300;
  font-size: 1.9rem;
  color: #2f5572; }
  @media (min-width: 530px) {
    .headline {
      font-size: 2.875rem; } }
  .headline--orange {
    color: #d59541; }
  .headline--light {
    font-weight: 100; }
  .headline--small {
    font-size: 1.2rem; }
    @media (min-width: 530px) {
      .headline--small {
        font-size: 1.875rem; } }
  .headline--no-t-margin {
    margin-top: 0; }
  .headline strong {
    font-weight: 400; }
  .headline--narrow {
    max-width: 31.25rem;
    margin: 0 auto; }
  .headline--margin-b-small {
    margin-bottom: 0.5em; }
  .headline--margin-b-large {
    margin-bottom: 1.6em; }

.row::after {
  content: "";
  clear: both;
  display: table; }

.row--t-padding {
  padding-top: 80px; }

.row--gutters {
  margin-right: -4rem; }

.row--gutters-large {
  margin-right: -6.25rem; }

.row--gutters-small {
  margin-right: -45px; }

.row--gutters > div {
  padding-right: 4rem; }

.row--gutters-large > div {
  padding-right: 6.25rem; }

.row--gutters-small > div {
  padding-right: 45px; }

.row__b-margin-until-medium {
  margin-bottom: 1rem; }

@media (min-width: 800px) {
  .row__b-margin-until-medium {
    margin-bottom: 0; }
  .row__medium-4 {
    float: left;
    width: 33.33%; }
  .row__medium-4-larger {
    width: 37%; }
  .row__medium-8 {
    float: left;
    width: 66.66%; }
  .row__medium-8-smaller {
    width: 63%; }
  .row__medium-6 {
    float: left;
    width: 50%; } }

@media (min-width: 1010px) {
  .row__b-margin-until-large {
    margin-bottom: 0; }
  .row__large-4 {
    float: left;
    width: 33.33%; }
  .row__large-4-larger {
    width: 37%; }
  .row__large-8 {
    float: left;
    width: 66.66%; }
  .row__large-8-smaller {
    width: 63%; }
  .row__large-6 {
    float: left;
    width: 50%; } }

@media (min-width: 1010px) {
  .row--equal-height-at-large-screen {
    display: flex; }
    .row--equal-height-at-large-screen > div {
      float: none;
      display: flex; } }

.generic-content p {
  line-height: 1.65;
  font-weight: 300;
  margin: 0 0 1.8rem 0; }
  @media (min-width: 530px) {
    .generic-content p {
      font-size: 1.1875rem; } }
  .generic-content p a {
    font-weight: 700; }

.section-title {
  font-weight: 300;
  text-align: center;
  font-size: 2.875rem; }
  @media (min-width: 530px) {
    .section-title {
      font-size: 3.75rem; } }
  .section-title--blue {
    color: #2f5572; }
  .section-title__icon {
    display: block;
    margin: 0 auto 0.8rem; }
    @media (min-width: 530px) {
      .section-title__icon {
        margin-right: 0.6rem;
        display: inline-block;
        margin-bottom: 0;
        position: relative;
        top: 8px; } }

.feature-item {
  position: relative;
  padding-bottom: 2.5rem; }
  @media (min-width: 530px) {
    .feature-item {
      padding-left: 5.875rem; } }
  .feature-item__icon {
    display: block;
    margin: auto; }
    @media (min-width: 530px) {
      .feature-item__icon {
        position: absolute;
        left: 0; } }
  .feature-item__title {
    font-weight: 300;
    font-size: 1.5rem;
    margin-bottom: 0.8rem;
    margin-top: 0.7em;
    text-align: center; }
    @media (min-width: 530px) {
      .feature-item__title {
        font-size: 1.875rem;
        text-align: left;
        margin-top: 1em; } }

.page-section {
  padding: 1.2rem 0; }
  .page-section--blue {
    background-color: #2f5572;
    color: #fff; }
  .page-section--testimonials {
    background-color: #e0e6ef; }
    @media (min-width: 1010px) {
      .page-section--testimonials {
        background: url(../imgs/testimonials-bg.jpg) top center no-repeat;
        background-size: cover; } }
  @media (min-width: 800px) {
    .page-section {
      padding: 4.5rem 0; } }
  .page-section--no-b-padding {
    padding-bottom: 0; }
    @media (min-width: 1010px) {
      .page-section--no-b-padding {
        padding: 4.5rem 0; } }

.testimonial {
  background-color: rgba(255, 255, 255, 0.8);
  padding: 0 15px 1px 15px;
  margin-bottom: 98px; }
  @media (min-width: 1010px) {
    .testimonial {
      margin-bottom: 0;
      padding: 0 1.8rem 1px 1.8rem; } }
  .testimonial__photo {
    width: 160px;
    height: 160px;
    border-radius: 50%;
    overflow: hidden;
    border: 6px solid rgba(255, 255, 255, 0.8);
    position: relative;
    top: -80px;
    margin: 0 auto -80px; }
  .testimonial__title {
    font-size: 1.5rem;
    color: #2f5572;
    margin: 0;
    font-weight: 500; }
  .testimonial__subtitle {
    font-size: 0.975rem;
    color: #d59541;
    margin: 0.25rem auto 1rem;
    font-weight: 500; }
  .testimonial--last {
    margin-bottom: 0; }

